import { Component,Input,OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {RouteSegment} from '@angular/router';
@Component({
    moduleId: module.id,
    selector: 'app-mediaplan-summary',
    templateUrl: 'mediaplan-summary.component.html',
    styleUrls: ['mediaplan-summary.component.css'],
    inputs: ['planInput', 'productInput', 'audienceInput', 'mediaPlan', 'businessObjectiveInput', 'kpiToMeasureInput', 'showAddedInput', 'showUserInputs', 'client']
})
export class MediaplanSummaryComponent implements OnInit{

    planInput:any = {};
    productInput:any = {};
    audienceInput:any = {};
    mediaPlan:any = {};
    businessObjectiveInput:any = {};
    kpiToMeasureInput:any = {};
    client:string;
    toggleSummary:string = "+";
    
    public ngOnInit() {
       
    }
    public toggleSummaryPlan() {
        if(this.toggleSummary === '+') {
            this.toggleSummary = '-';
        }else {
            this.toggleSummary = '+';
        }
    }

}
